import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TransportService } from './transport.service';
import { Observable } from 'rxjs/Observable';
import { CommonProperties } from '../common-properties';
import 'rxjs/add/operator/takeWhile';

import { CommonService } from '../../services/common.service';
import { Http, Response, Headers, RequestOptions } from '@angular/http';

@Component({
  moduleId: module.id,
  selector: 'transport',
  templateUrl: './transport.component.html',
  styleUrls: ['./transport.component.css'],
})

export class TransportComponent implements OnInit, OnDestroy {
  
  isSubscribe: boolean = true;

  id: number;
  transportSelected: any= {};
  transportList: any;
  message: string;
  isErrorMessage: boolean= false;
  transportname: string;
  objCurrentDate: Date;
 
  actives: any = [
    { id: 0, name: 'False' },
    { id: 1, name: 'True' },
  ];
  
  types: any = [
    { id: 'BOYS', name: 'BOYS' },
    { id: 'GIRLS', name: 'GIRLS' },
    { id: 'MIXED', name: 'MIXED' },
  ];

  faculties: any;
  classes: any;
  

 returnUrl = `transport/managetransport/`;  
 selectedItem: any;

  constructor(private route: ActivatedRoute, private router: Router, private transportService: TransportService, 
    private commonService: CommonService, private _http: Http, private commonProperties: CommonProperties) { }

  ngOnInit() {
   // console.log(module.id);
    // console.log(JSON.parse(localStorage.getItem('currentUser')));
   // console.log(this.commonProperties.currentUser.ID );
    this.objCurrentDate = new Date();
    this.route.params.subscribe(
      params => {
             this.id = +params['id']; // (+) converts string 'id' to a number
      });
   // get the Transport to update
    if (this.id > 0) {
    this.getTransportByID(this.id);
    }
  }
 
  ngOnDestroy() {
    this.isSubscribe = false;
  }
// end for the same 

  // Get Transport
  getTransport() {
          this.transportService.getTransport()
          .takeWhile(() => this.isSubscribe)
                          .subscribe(resData => this.transportList = resData,
                            err => console.error(err),
                          // () => console.log(this.transportList)
                          );
  }
                        
getTransportByID(id) {
            this.transportService.getTransportByID(id)
            .takeWhile(() => this.isSubscribe)
              .subscribe(resData => { 
                this.transportSelected = resData; 
                this.transportSelected.IsActive = parseInt(this.transportSelected.IsActive);
              },
                err => console.error(err),
      );
                            
}

  HideMessage() {
    this.message = '';
    // return this.isErrorMessage = !this.isErrorMessage;
  }

  // Update Transport
  updateTransport(id, objTransport) {
    objTransport.ID = id;
  // objTransport.LastUpdatedBy = JSON.parse(localStorage.getItem('currentUser')).ID;
    objTransport.LastUpdatedBy = this.commonProperties.currentUser.ID;
    objTransport.LastModifiedOn = new Date(); 
    this.transportService.updateTransport(id, objTransport)
    .takeWhile(() => this.isSubscribe)
    .subscribe (
       data => {
         // refresh the list
        // this.getTransport();
         this.message = 'Transport updated successfully! ';
        // this.router.navigate([this.returnUrl]);
         return true;
       },
       error => {
         console.error('Error saving Transport!');
         return Observable.throw(error);
       },
    );

  }

SuperAd(isSuperAdmin) {
  // console.log(isSuperAdmin);
 // console.log(this.selectedItem);
  // alert(isSuperAdmin.value);
}

onBack() {
  this.router.navigate([this.returnUrl]);
}

 saveTransport (objTransport: any) {
  objTransport.CreatedBy = this.commonProperties.currentUser.ID; // JSON.parse(localStorage.getItem('currentUser')).ID;
  objTransport.CreatedOn = new Date(); 
  this.transportService.saveTransport(objTransport)
  .takeWhile(() => this.isSubscribe)
                    .subscribe(
                      resData => {
                        // this.getTransport();
                        this.isErrorMessage = false;
                        this.transportname = '';
                        this.message = 'Data saved successfully! ';
                      },
                      error => {
                        this.isErrorMessage = true;
                        this.message = `Error ${error}`;
                        console.error('Error saving Transport!');
                        return Observable.throw(error);
                  });
  }

  // Save Transport Record
  submitTransport(value: any) {
  // console.log(value.value);
    if (!value.form.valid) {
      this.message = 'Please enter required values!';
      this.isErrorMessage = true;
      this.transportname = 'danger';
        return;
    }
     
    // update the Transport record
    if (this.id > 0) {
          this.updateTransport(this.id, value.value);  
          value.form.reset();
    } else {
          this.saveTransport(value.value);
          value.form.reset();
      }
  }

}



