import { NgModule } from '@angular/core';
import { CommonModule, LowerCasePipe } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { routing } from './transport.routing';
import { HttpModule } from '@angular/http';
import { NgaModule } from '../../theme/nga.module';

import { TransportComponent } from './transport.component';
import { TransportManageComponent } from './transport-manage.component';
import { TransportService } from './transport.service';

// import { SearchFilterPipe } from 'app/services/search-filter.pipe';
import { PaginationService } from '../../services/pagination.service';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    routing,
    NgaModule,
    HttpModule,
  ],
  declarations: [TransportComponent, TransportManageComponent],
  providers: [TransportService, PaginationService],
})

export class TransportModule {


 }



