import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
// import { AsyncPipe } from '@angular/common';
import 'rxjs/add/operator/takeWhile';
import { TransportService } from './transport.service';
import { Observable } from 'rxjs/Observable';
import { CommonProperties } from '../common-properties';

import { PaginationService } from '../../services/pagination.service';


@Component({
  selector: 'transport-manage',
  templateUrl: './transport-manage.component.html',
  // styleUrls: ['./smartTables.scss'],
   styleUrls: ['./transport.component.css'],
  
})
export class TransportManageComponent implements OnInit, OnDestroy {

  isSubscribe: boolean = true;
 transportServiceUrl: string;
  title = 'Manage Transport';
  itemList: any;
  query: string = '';
  returnUrl: string;
  transportID: number;

  constructor(private transportService: TransportService, private route: ActivatedRoute, private router: Router, 
              private paginationService: PaginationService ) { }

  ngOnInit() {
    this.transportServiceUrl = CommonProperties.serviceURL;
    this.getTransport();
  }
  ngOnDestroy() {
    this.isSubscribe = false;
  }
  // New Transport
  newTransport() {
    this.router.navigate(['transport/transport/']);
  }

// Get Transport
  getTransport() {
    $('#popup').fadeIn();
    $('#fade').fadeIn();
          this.transportService.getTransport()
                          .takeWhile(() => this.isSubscribe)
                          .subscribe(
                            resData => {
                            this.paginationService.itemList = resData;
                            $('#popup').fadeOut();
                            $('#fade').fadeOut();
                            },
                            err => console.error(err),
                            () => { 
                              this.paginationService.filteredItems = this.paginationService.itemList;
                              this.paginationService.init();
                           },
                      );
  }

  // Update Transport
  updateTransport(id) {
    this.returnUrl = `transport/transport/${id}`;  
    this.router.navigate([this.returnUrl]);
  }

  setTransportID(id): void {
   // console.log(id);
    this.transportID = id;
  }
  // Delete Transport
  deleteTransport(): void {
      this.transportService.deleteTransport(this.transportID)
      .takeWhile(() => this.isSubscribe)
      .subscribe(
         data => {
           // refresh the list
           this.getTransport();
           return true;
         },
         error => {
           console.error('Error deleting Transport!');
           return Observable.throw(error);
         },
      );
  }

}



