import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';

import { CommonProperties } from '../common-properties';
import { xhrHeaders } from '../xhr-headers';
import { Observable } from 'rxjs/Observable';
import { CommonService } from 'app/services/common.service';

@Injectable()
export class TransportService {

    private transportServiceUrl = `${CommonProperties.serviceURL}api/Transports/`; 
    // preferred to use template literal rather concatination  like below line
    // CommonProperties.serviceURL + 'api/admins/';  // URL to web api 

    constructor(private http: Http, private commonService: CommonService) {}

    getTransport(): Observable<any> {
        return this.http.get(this.transportServiceUrl)
                        .map((res: Response) => res.json());
    }
   
    getTransportByID(id) {
        return this.http.get(`${this.transportServiceUrl}${id}`)
                .map((res: Response) => res.json());
    }
   
    saveTransport(objTransport) {
        const body = JSON.stringify(objTransport);
        return this.http.post(this.transportServiceUrl, body, xhrHeaders())
            .map((res: Response) => res.json());
    }

    deleteTransport(id) {
        return this.http.delete(`${this.transportServiceUrl}${id}`);
   }

  updateTransport(id, objTransport) {
      const body = JSON.stringify(objTransport);
      return this.http.put(`${this.transportServiceUrl}${id}`, body, xhrHeaders()).map((res: Response) => res.json());
  }
  
}



